@media all and (min-width:0px) and (max-width: 768px) {
 
  .logo {
    height: 220px !important;
    width: auto !important;
    margin: 0;
  }

  .link {
    margin-bottom: 40px;
  }

}


body{
  overflow: hidden;
}


.App {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin: 0;
  
}


.content-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.logo{
  height: 500px;
  margin: 70px;
  fill: black;
  
}

.App-logo {
  margin-top: 300px;
  margin-bottom: 0;
  scale: .3;
}

.error-text {
  color: red;
}

.link {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  margin-top: 40px;
  color: white;
  background-color: black;
  width: 150px;
  height: 40px;
  border-radius: 5px;
}




